        --background: 240 100% 1.96%;
        --foreground: 213 31% 91%;

        --muted: 240 3.7% 15.9%;
        --muted-foreground: 240 3% 38%;
        --accent: 240 5% 26%;
        --accent-foreground: 240 5% 26%;

