import React, { useEffect } from 'react';
import { testIds } from '../components/testIds';
import { PluginPage } from '@grafana/runtime';
import DiscoverHeader from '../components/discover-header';
import '../styles/tailwind.css';
import DiscoverFilter from 'components/discover-filter';
import DiscoverSidebar from 'components/discover-sidebar';
import { css } from '@emotion/css';
import { DiscoverHistogram } from 'components/discover-histogram';
import { LoadingBar, useTheme2 } from '@grafana/ui';
import DiscoverContent from 'components/discover-content';
import { useAtom, useAtomValue, useSetAtom } from 'jotai';
import {
    currentCatalogAtom,
    // currentClusterAtom,
    currentDatabaseAtom,
    currentDateAtom,
    currentIndexAtom,
    currentTableAtom,
    currentTimeFieldAtom,
    dataFilterAtom,
    discoverLoadingAtom,
    // datasourcesAtom,
    // discoverCurrentAtom,
    // dorisInfoAtom,
    intervalAtom,
    pageAtom,
    pageSizeAtom,
    searchTypeAtom,
    searchValueAtom,
    selectedDatasourceAtom,
    // selectedIndexesAtom,
    tableDataAtom,
    tableDataChartsAtom,
    tableFieldsAtom,
    tableTotalCountAtom,
    tableTracesDataAtom,
    topDataAtom,
} from 'store/discover';
// import { lastValueFrom } from 'rxjs';
import { getTableDataChartsService, getTableDataCountService, getTableDataService, getTableDataTraceService, getTopDataService } from 'services/discover';
import { FORMAT_DATE, getAutoInterval, IntervalEnum } from '../constants';
import { Dayjs } from 'dayjs';
import { getIndexesStatement, encodeBase64, getChartsData, convertColumnToRow, generateHighlightedResults, formatTracesResData, convertColumnToRowViaFieldsType } from 'utils/data';
import { generateTableDataUID } from 'utils/utils';

export default function PageDiscover() {
    // const [getTableDataLoading] = React.useState(false);
    const [page, setPage] = useAtom(pageAtom);
    const pageSize = useAtomValue(pageSizeAtom);
    const setTableData = useSetAtom(tableDataAtom);
    const setTableDataCharts = useSetAtom(tableDataChartsAtom);
    const selectdbDS = useAtomValue(selectedDatasourceAtom);
    const currentTimeField = useAtomValue(currentTimeFieldAtom);
    const interval = useAtomValue(intervalAtom);
    const currentIndexes = useAtomValue(currentIndexAtom);
    const tableFields = useAtomValue(tableFieldsAtom);
    const searchType = useAtomValue(searchTypeAtom);
    const dataFilter = useAtomValue(dataFilterAtom);
    const searchValue = useAtomValue(searchValueAtom);
    const setTopData = useSetAtom(topDataAtom);
    // const currentCluster = useAtomValue(currentClusterAtom);
    const currentTable = useAtomValue(currentTableAtom);
    const currentCatalog = useAtomValue(currentCatalogAtom);
    const currentDatabase = useAtomValue(currentDatabaseAtom);
    const currentDate = useAtomValue(currentDateAtom);
    const setTableTotalCount = useSetAtom(tableTotalCountAtom);
    const setTraceData = useSetAtom(tableTracesDataAtom);
    const [loading, setLoading] = useAtom(discoverLoadingAtom);

    // const [appInfo, setAppInfo] = useAtomValue(dorisInfoAtom);

    // const shouldUseClusterParam = () => enableCloudNative(appInfo?.version, appInfo?.supportComputeGroup);

    // const discoverCurrent = useAtomValue(discoverCurrentAtom);

    const theme = useTheme2();

    useEffect(() => {
        if (currentTimeField) {
            getTableData();
            getTopData();
            getTableDataCharts();
            getTableDataCount();
        } else {
            setTableDataCharts([]);
            setTableTotalCount(NaN);
            setTableData([]);
            setTopData([]);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [currentTimeField, page]);
    function getTableData() {
        setLoading(prev => ({ ...prev, getTableData: true }));
        const indexesStatement = getIndexesStatement(currentIndexes, tableFields, searchValue);
        let payload: any = {
            catalog: currentCatalog,
            database: currentDatabase,
            table: currentTable,
            timeField: currentTimeField,
            startDate: currentDate[0]?.format(FORMAT_DATE),
            endDate: (currentDate[1] as Dayjs).format(FORMAT_DATE),
            cluster: '',
            sort: 'DESC',
            search_type: searchType,
            indexes: '',
            page: page,
            page_size: pageSize,
        };
        if (searchType === 'Search') {
            payload.indexes_statement = indexesStatement;
        }
        if (dataFilter.length > 0) {
            payload.data_filters = dataFilter;
        } else {
            payload.data_filters = [];
        }
        if (searchValue) {
            payload.search_value = searchType === 'Search' ? encodeBase64(searchValue) : searchValue;
        }

        getTableDataService({
            selectdbDS,
            ...payload,
        }).subscribe({
            next: async ({ data, ok }: any) => {
                setLoading(prev => ({ ...prev, getTableData: false }));
                if (ok) {
                    const rowsData = convertColumnToRow(data);

                    const resData = generateHighlightedResults(
                        {
                            search_value: searchValue,
                            indexes: currentIndexes || [],
                        },
                        rowsData,
                    );

                    const rowsDataWithUid = await generateTableDataUID(resData);

                    setTableData(rowsDataWithUid);
                }
            },
            error: (err: any) => {
                setLoading(prev => ({ ...prev, getTableData: false }));
                console.log('', err);
            },
        });
    }

    function getTableDataCharts() {
        setLoading(prev => ({ ...prev, getTableDataCharts: true }));
        const timeInterval = interval === IntervalEnum.Auto ? getAutoInterval(currentDate as any).interval_unit : interval;
        const timeIntervalValue = interval === IntervalEnum.Auto ? getAutoInterval(currentDate as any).interval_value : 1;
        const indexesStatement = getIndexesStatement(currentIndexes, tableFields, searchValue);
        let payload: any = {
            catalog: 'internal',
            database: currentDatabase,
            table: currentTable,
            timeField: currentTimeField,
            startDate: currentDate[0]?.format(FORMAT_DATE),
            endDate: (currentDate[1] as Dayjs).format(FORMAT_DATE),
            cluster: '',
            // cluster: shouldUseClusterParam() ? currentCluster : '',
            sort: 'DESC',
            interval: timeInterval,
            interval_value: timeIntervalValue,
            search_type: searchType,
            indexes: indexesStatement,
        };
        if (dataFilter.length > 0) {
            payload.data_filters = dataFilter;
        }
        if (searchValue) {
            payload.search_value = searchType === 'Search' ? encodeBase64(searchValue) : searchValue;
        }
        getTableDataChartsService({
            selectdbDS,
            ...payload,
        }).subscribe({
            next: ({ data, ok }: any) => {
                setLoading(prev => ({ ...prev, getTableDataCharts: false }));
                if (ok) {
                    const value = data?.results[0]?.data?.values[0];
                    if (!value || value.length === 0) {
                        setTableDataCharts([]);
                        return;
                    }
                    const tableDataCharts = data?.results[0]?.data?.values[0].map((item: any, index: number) => {
                        return {
                            TT: item,
                            'sum(cnt)': data?.results[0]?.data?.values[1][index],
                        };
                    });
                    const chartsData = getChartsData(tableDataCharts, currentDate as [Dayjs, Dayjs]);
                    setTableDataCharts(chartsData);
                }
            },
            error: (err: any) => {
                setLoading(prev => ({ ...prev, getTableDataCharts: false }));
                console.log('', err);
            },
        });
    }

    function getTopData() {
        const indexesStatement = getIndexesStatement(currentIndexes, tableFields, searchValue);
        let payload: any = {
            catalog: currentCatalog,
            database: currentDatabase,
            table: currentTable,
            timeField: currentTimeField,
            startDate: currentDate[0]?.format(FORMAT_DATE),
            endDate: (currentDate[1] as Dayjs).format(FORMAT_DATE),
            cluster: '',
            sort: 'DESC',
            search_type: searchType,
            indexes: '',
            page: page,
            page_size: 500,
        };
        if (searchType === 'Search') {
            payload.indexes_statement = indexesStatement;
        }
        if (dataFilter.length > 0) {
            payload.data_filters = dataFilter;
        } else {
            payload.data_filters = [];
        }
        if (searchValue) {
            payload.search_value = searchType === 'Search' ? encodeBase64(searchValue) : searchValue;
        }
        getTopDataService({
            selectdbDS,
            ...payload,
        }).subscribe({
            next: ({ data, ok }: any) => {
                if (ok) {
                    const rowsData = convertColumnToRowViaFieldsType(data, tableFields);
                    console.log('rowsData', rowsData)
                    setTopData(rowsData);
                }
            },
            error: (err: any) => {
                console.log('', err);
                setTopData([]);
            },
        });
    }

    function getTableDataCount() {
        const timeInterval = interval === IntervalEnum.Auto ? getAutoInterval(currentDate as any).interval_unit : interval;
        const timeIntervalValue = interval === IntervalEnum.Auto ? getAutoInterval(currentDate as any).interval_value : 1;
        const indexesStatement = getIndexesStatement(currentIndexes, tableFields, searchValue);
        let payload: any = {
            catalog: 'internal',
            database: currentDatabase,
            table: currentTable,
            timeField: currentTimeField,
            startDate: currentDate[0]?.format(FORMAT_DATE),
            endDate: (currentDate[1] as Dayjs).format(FORMAT_DATE),
            cluster: '',
            // cluster: shouldUseClusterParam() ? currentCluster : '',
            sort: 'DESC',
            interval: timeInterval,
            interval_value: timeIntervalValue,
            search_type: searchType,
            indexes: indexesStatement,
        };
        if (dataFilter.length > 0) {
            payload.data_filters = dataFilter;
        }
        if (searchValue) {
            payload.search_value = searchType === 'Search' ? encodeBase64(searchValue) : searchValue;
        }
        getTableDataCountService({
            selectdbDS,
            ...payload,
        }).subscribe({
            next: ({ data, ok }: any) => {
                if (ok) {
                    const total_count = data?.results[0]?.data?.values[0];
                    if (!total_count) {
                        setTableTotalCount(0);
                        return;
                    }
                    setTableTotalCount(total_count);
                }
            },
            error: (err: any) => {
                console.log('', err);
            },
        });
    }

    function getTraceData(trace_id: string) {
        const indexesStatement = getIndexesStatement(currentIndexes, tableFields, searchValue);
        let payload: any = {
            catalog: currentCatalog,
            database: currentDatabase,
            table: 'otel_traces', // 
            timeField: currentTimeField,
            startDate: currentDate[0]?.format(FORMAT_DATE),
            endDate: (currentDate[1] as Dayjs).format(FORMAT_DATE),
            cluster: '',
            sort: 'DESC',
            search_type: searchType,
            indexes: '',
            page: page,
            page_size: pageSize,
            trace_id,
        };
        if (searchType === 'Search') {
            payload.indexes_statement = indexesStatement;
        }
        if (dataFilter.length > 0) {
            payload.data_filters = dataFilter;
        } else {
            payload.data_filters = [];
        }
        if (searchValue) {
            payload.search_value = encodeBase64(searchValue);
        }

        getTableDataTraceService({
            selectdbDS,
            ...payload,
        }).subscribe({
            next: ({ data, ok }: any) => {
                if (ok) {
                    const formatedData = formatTracesResData(data);
                    setTraceData(formatedData);
                }
            },
            error: (err: any) => {
                console.log('', err);
            },
        });
    }

    useEffect(() => {
        setPage(1);
        if (currentTimeField) {
            getTableDataCharts();
            getTableDataCount();
            getTableData();
            getTopData();
        } else {
            setTableDataCharts([]);
            setTableTotalCount(NaN);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [currentDate, currentTimeField, dataFilter, interval]);

    return (
        <div
            className={css`
                & > div > div {
                    background-color: ${theme.isDark ? '#111217' : '#F4F5F5'};
                    padding: 0 1rem;
                }
            `}
        >
            <PluginPage pageNav={{ text: '' }}>
                <div data-testid={testIds.pageTwo.container}>
                    <DiscoverHeader
                        onQuerying={() => {
                            if (currentTimeField) {
                                setPage(1);
                                getTopData();
                                getTableDataCharts();
                                getTableDataCount();
                                getTableData();
                            }
                        }}
                        loading={loading.getTableData || loading.getTableDataCharts}
                    />
                    <DiscoverFilter />
                </div>
                <section
                    className={css`
                        display: grid;
                        grid-template-columns: 2fr 8fr;
                        grid-template-rows: 1fr;
                        grid-template-areas: 'sidebar content';
                        padding-top: 0.5rem;
                        gap: 0.5rem;
                    `}
                >
                    <div
                        style={{
                            height: 'calc(100vh - 210px)',
                        }}
                    >
                        <DiscoverSidebar />
                    </div>
                    <div
                        className={css`
                            grid-area: content;
                            display: flex;
                            flex-direction: column;
                            overflow: auto;
                            height: calc(100vh - 210px);
                            background-color: ${theme.isDark ? 'rgb(24, 27, 31)' : '#FFF'};
                            position: relative;
                            padding: 16px 0;
                        `}
                    >
                        <div style={{ position: 'absolute', top: 0, width: '100%' }}>
                            {loading.getTableDataCharts && <LoadingBar width={100} />}
                        </div>
                        <DiscoverHistogram />
                        <div
                            className={css`
                                margin-top: 24px;
                            `}
                        >
                            <DiscoverContent getTraceData={getTraceData} fetchNextPage={() => {}} />
                        </div>
                    </div>
                </section>
            </PluginPage>
        </div>
    );
}
